<template>
	<div id="merchant-report">
		<public-header title="投诉列表"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">已处理</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">未处理</div>
				</div>
				<van-search placeholder="请输入商户名称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="current-list"
			>
				<div class="report-item" v-for="(item, index) in records.data" :key="item.id">
				<div class="padding-10">
					<div class="flex-lr padding-10-b">
						<div class="font-bold">{{item.store_title}}</div>
						<div class="itag itag-primary" v-if="item.status == 1">已处理</div>
						<div class="itag itag-disabled" v-else-if="item.status == 0">未处理</div>
					</div>
					<div class="report-info flex">
						<div>投诉人手机号：</div>
						<div class="c-info" @click="util.jsUrl('tel:' + item.mobile)">{{item.mobile}} <i class="icon icon-phone2"></i></div>
					</div>
					<div class="report-info flex">
						<div>投诉时间：</div>
						<div>{{item.addtime_cn}}</div>
					</div>
					<div class="report-info flex">
						<div>投诉内容：</div>
					</div>
					<div class="replay">
						<div>{{item.title}}</div>
						<div class="replay-img" v-if="item.thumbs">
							<div class="img-item" v-for="(img, index) in item.thumbs"  @click="util.jsPreviewImage(item.thumbs)">
								<img class="img-100" :src="img" alt="">
							</div>
						</div>
					</div>
				</div>
				<div class="border-1px-t btn-group">
					<div class="btn-item bg-default" v-if="item.status == 1" @click="onChangeStatus(item, 0, index, '未处理')">设为未处理</div>
					<div class="btn-item bg-primary" v-if="item.status == 0" @click="onChangeStatus(item, 1, index, '已处理')">设为已处理</div>
				</div>
			</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading: true,
				isRefresh: false,
				filter: {
					items: {
						status: '-1'
					}
				},
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/merchant/report/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onChangeStatus(item, status, index, text){
				var that = this;
				that.util.jspost({
					url: 'plateform/merchant/report/status',
					data: {
						id: item.id,
						status: status
					},
					confirm: '确定修改处理状态吗',
					success:function(){
						that.records.data[index].status = status;
						that.records.data[index].status_cn = text;
					}
				});
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#merchant-report .content .van-search{
		padding: 10px;
	}
	#merchant-report .content .report-item{
		background-color: #fff;
		margin-bottom: 10px;
	}
	#merchant-report .content .report-item .report-info{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 8px;
	}
	#merchant-report .content .report-item .report-info .icon-phone2{
		font-size: 16px;
		padding-left: 5px;
	}
	#merchant-report .content .report-item .replay{
		font-size:14px;
		color:#898989;
		padding:10px;
		background:#F4F4F4;
	}
	#merchant-report .content .report-item .replay .replay-img{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-start;
		align-items: center;
		width: 100%;
		padding-top: 10px;
	}
	#merchant-report .content .report-item .replay .replay-img .img-item{
		width: 25%;
		padding-right: 10px;
	}
	#merchant-report .content .report-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#merchant-report .content .report-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


